import { getYMDFormateTime, isArrayType } from "../../../js/utility.js"
import { mapState } from 'vuex'
import PlateType from '../../../js/enum.js'
import event from '../../../js/Event.js'

export default {
  name: 'moresort',
  components: {},
  computed: {
    ...mapState({
      userInfo: state => state.userInfo
    }),
    itemalllist() {
      var tempAllList = {};
      if (this.userInfo.leads || this.userInfo.admin === 1) {
        tempAllList['leads'] = {
          title: '线索',
          type: PlateType.Leads,
          field: 'leads',
          path: 'list/' + PlateType.Leads + '/default',
          url: require('../../../assets/xs.png')
        };
      }
      if (this.userInfo.customer || this.userInfo.admin === 1) {
        tempAllList['customer'] = {
          title: '客户',
          field: 'customer',
          type: PlateType.Customer,
          path: 'list/' + PlateType.Customer + '/default',
          url: require('../../../assets/index_customer.png')
        };
        tempAllList['customerpool'] = {
          title: '客户池',
          field: 'customerpool',
          type: PlateType.CustomerPool,
          path: 'list/' + PlateType.CustomerPool + '/default',
          url: require('../../../assets/khc.png')
        };
      }
      if (this.userInfo.contacts || this.userInfo.admin === 1) {
        tempAllList['contacts'] = {
          title: '联系人',
          field: 'contacts',
          type: PlateType.Contacts,
          path: 'list/' + PlateType.Contacts + '/default',
          url: require('../../../assets/lxr.png')
        };
      }
      if (this.userInfo.business || this.userInfo.admin === 1) {
        tempAllList['business'] = {
          title: '商机',
          field: 'business',
          type: PlateType.Business,
          path: 'list/' + PlateType.Business + '/default',
          url: require('../../../assets/sj.png')
        };
      }
      if (this.userInfo.contract || this.userInfo.admin === 1) {
        tempAllList['contract'] = {
          title: '合同',
          field: 'contract',
          type: PlateType.Contract,
          path: 'list/' + PlateType.Contract + '/default',
          url: require('../../../assets/sht.png')
        };
      }
      if (this.userInfo.product || this.userInfo.admin === 1) {
        tempAllList['product'] = {
          title: '产品',
          field: 'product',
          type: PlateType.Product,
          path: 'list/' + PlateType.Product + '/default',
          url: require('../../../assets/cp.png')
        };
      }
      if (this.userInfo.finance || this.userInfo.admin === 1) {
        tempAllList['finance'] = {
          title: '财务',
          field: 'finance',
          type: PlateType.Finance,
          path: 'financemain',
          url: require('../../../assets/caiwu.png')
        };
      }

      return tempAllList;
    }
  },
  data() {
    return {
      topList: [],
      bottomList: [],
    }
  },
  beforeDestroy() {
    event.$off("saveDone");
  },
  mounted() {
    event.$on('saveDone', function() {
      this.saveInfos();
    }.bind(this));

    var wkmoresort = window.localStorage.wkmoresort;
    if (wkmoresort) {
      var sortArray = wkmoresort.split(",");
      for (var i = 0; i < sortArray.length; i++) {
        var item = sortArray[i];
        var addItem = this.itemalllist[item];
        if (addItem) {
          addItem.select = true;
          this.topList.push(addItem);
        }
      }

      for (var key in this.itemalllist) {
        var item = this.itemalllist[key];
        if (item.select) {

        } else {
          this.bottomList.push(item);
        }
      }



    } else {
      for (var key in this.itemalllist) {
        if (this.topList.length < 7) {
          this.topList.push(this.itemalllist[key]);
        } else {
          this.bottomList.push(this.itemalllist[key]);
        }
      }
    }
  },
  methods: {
    addItem(item, index) {
      if (this.topList.length < 7) {
        this.topList.push(item);
        this.bottomList.splice(index, 1);
      }
    },
    deleteItem(item, index) {
      if (this.topList.length > 1) {
        this.bottomList.push(item);
        this.topList.splice(index, 1);
      }
    },
    saveInfos() {
      var tempwkmoresort = '';
      for (var i = 0; i < this.topList.length; i++) {
        var item = this.topList[i];
        if (i === this.topList.length - 1) {
          tempwkmoresort = tempwkmoresort + item.field;
        } else {
          tempwkmoresort = tempwkmoresort + item.field + ',';
        }
      }
      window.localStorage.wkmoresort = tempwkmoresort;
      this.$router.go(-1);
    }
  }
}




